.overlay {
  position: fixed;
  content: "";
  z-index: 20;

  display: none;

  width: 100%;
  height: 100%;

  background-color: $light-beige;

  &--cart {
    background-color: rgba($color: #000000, $alpha: 0.7);
  }

  &--shown {
    display: block;
  }
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 30;

  display: none;

  opacity: 0;

  transform: translate(-50%, -50%);

  transition: all 0.25s;

  &--active {
    display: block;

    opacity: 1;
  }
}
